.weather-container {
  padding: 20px 0;
}

.loading-text {
  text-align: center;
  padding: 20px;
  color: #666;
  font-size: 1.2rem;
}

.forecast-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
  gap: 25px;
  padding: 20px;
}

.weather-card {
  background: #ffffff;
  border-radius: 15px;
  padding: 25px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s ease-in-out;
  cursor: pointer;
}

.weather-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

.icon-container {
  text-align: center;
  margin-bottom: 20px;
}

.weather-icon {
  width: 100px;
  height: 100px;
  object-fit: contain;
}

.date-text {
  font-size: 1.1rem;
  color: #666;
  margin-bottom: 12px;
  font-weight: 500;
  text-align: center;
  letter-spacing: 0.5px;
}

.temperature {
  font-size: 2.2rem;
  font-weight: 700;
  color: #2c3e50;
  text-align: center;
  margin: 15px 0;
  position: relative;
}

.temperature::after {
  content: '';
  display: block;
  width: 40px;
  height: 2px;
  background: #eee;
  margin: 12px auto;
}

.description {
  font-size: 0.95rem;
  color: #7f8c8d;
  text-align: center;
  text-transform: capitalize;
  line-height: 1.4;
}

.city-header {
  text-align: center;
  color: #2c3e50;
  margin-bottom: 30px;
  font-size: 1.8rem;
  letter-spacing: -0.5px;
}

.error-message {
  color: #e74c3c;
  text-align: center;
  padding: 20px;
  font-weight: 500;
}
@media (max-width: 768px) {
  .forecast-grid {
    grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
    gap: 15px;
  }
  
  .weather-card {
    padding: 15px;
  }
  
  .weather-icon {
    width: 80px;
    height: 80px;
  }
}

@media (max-width: 480px) {
  .forecast-grid {
    grid-template-columns: 1fr;
  }
  
  .city-header {
    font-size: 1.5rem;
  }
}